import Vue from 'vue'
import VueRouter from 'vue-router'
import Index from "../views/Index";
import Course from "../views/Course";
Vue.use(VueRouter)

const routes = [
  {
    path:'/',
    name:'/',
    component:Index
  },
  {
    path: "/course",
    name: "/course",
    component: Course,
  }
]

const router = new VueRouter({
  mode:'hash',
  base:process.env.Base_URL,
  routes
})

export  default  router

